import { Component, OnInit, Input, Output, EventEmitter, forwardRef, ViewChild, ElementRef } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

@Component({
  selector: 'mbaas-input-radio-img',
  templateUrl: './input-radio-img.component.html',
  styleUrls: ['./input-radio-img.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => InputRadioImgComponent),
      multi: true
    }
  ],
})
export class InputRadioImgComponent implements OnInit, ControlValueAccessor {
  @Output() check = new EventEmitter();
  // tslint:disable-next-line: no-input-rename tslint:disble-next-line: variable-name tslint:disable-next-line: no-inferrable-types
  @Input('value') _value: any;
  @Input() formControlValue: any;
  @Input() compraCartera: any = [];
  // tslint:disable-next-line: no-inferrable-types
  @Input() cartera: any = [];
  // tslint:disable-next-line: no-inferrable-types
  @Input() radioName: string = 'radioName';
  @ViewChild('radioComponent', { static: true }) radioComponent: ElementRef<HTMLElement>;

  showTasa: boolean;
  checked: boolean;

  onChange: any = () => { };
  onTouched: any = () => { };

  ngOnInit() {
  }

  get value() {
    return this._value;
  }

  set value(value) {
    this.checked = value === this.formControlValue;
    if (this.checked) {
      this._value = this.formControlValue;
      this.onChange(value);
      this.onTouched();
    }
  }

  writeValue(value) {
    this._value = this.formControlValue;
    this.checked = value === this.formControlValue;
    if (this.checked) {
      this.radioComponent.nativeElement.click();
    }
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

}
