import { Component, OnInit, OnDestroy, AfterViewInit } from '@angular/core';
import { BaseComponent } from '../../base/base.component';
import { BOTONES_FLUJO, CANALES, GTM } from 'src/mbaas/mbaas.const';
import { FormGroup } from '@angular/forms';

export interface CARD {
  COMPRA_CARTERA: string;
  SIN_COMPRA_CARTERA: string;
}

@Component({
  selector: 'mbaas-eva002',
  templateUrl: './eva002.component.html',
  styleUrls: ['./eva002.component.scss']
})
export class Eva002Component extends BaseComponent implements OnInit, AfterViewInit {
  CARD: CARD;
  conCompraCartera: any;
  show: boolean;

  ngOnInit() {
    this.CARD = {
      COMPRA_CARTERA: 'compraCartera',
      SIN_COMPRA_CARTERA: 'sinCompraCartera'
    };
    this.setRules();
  }

  /* GTM */
  ngAfterViewInit(): void {
    this.gtmService.createGtm(
      window,
      CANALES[this.payload.canal] + '-Tarjeta Móvil',
      GTM.PAGE_VIEW,
      '3. Selección de cupo aprobado'
    );
  }

  getDataToBack(): () => { [key: string]: any } {
    return (): { [key: string]: any } => ({
      atras: BOTONES_FLUJO.ATRAS
    });
  }

  getDataToContinue(): () => { [key: string]: any } {
    const F = this.formulario.getRawValue();
    return (): { [key: string]: any } => ({
      compraCartera: F.compraCartera === this.CARD.COMPRA_CARTERA ? true : false
    });
  }

  setRules() {
    super.setRules({
      compraCartera: ['', [this.rules.validationForRequired(this.getCondition())]]
    });
    this.show = true;
  }

  getCondition(): (control: FormGroup) => boolean {
    return (control: FormGroup) => {
      // if (!control.parent) { return false; }
      return !(control.value !== this.CARD.COMPRA_CARTERA
      || control.value !== this.CARD.SIN_COMPRA_CARTERA);
    };
  }

}
