import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Eva001Component } from './eva001/eva001.component';
import { Eva002Component } from './eva002/eva002.component';
import { MBAAS_STEPS } from 'src/mbaas/mbaas.route.const';
import { AccessStepGuard } from 'src/mbaas/core/guard/access-step.guard';


const routes: Routes = [
  {
    path: MBAAS_STEPS.EVA001.ROUTE,
    component: Eva001Component,
    canActivate: [
      // AccessStepGuard
    ],
    canDeactivate: [
      // AccessStepGuard
    ],
    data: {
      stepId: MBAAS_STEPS.EVA001.STEP
    }
  },
  {
    path: MBAAS_STEPS.EVA002.ROUTE,
    component: Eva002Component,
    canActivate: [
      // AccessStepGuard
    ],
    canDeactivate: [
      // AccessStepGuard
    ],
    data: {
      stepId: MBAAS_STEPS.EVA002.STEP
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EvaluadorRoutingModule { }
